import { useState } from 'react';

import HeaderComponent from '../components/header/header.js';

import Compiler from '../lib/compiler.js';
import { download, VM_ADDR, saveSource } from '../lib/vm-commands.js';
import { strToBytes } from '../lib/formatting.js';

function Header(props) {
    const [menuVisible, setMenuVisible] = useState(false);

    function toggleMenu (props) {
        setMenuVisible(!menuVisible);
    }

    const handleDownload = async () => {
        const str = props.procRef.current.value;
        const strBytes = strToBytes(str);
        const bytes = Compiler.downloadProcs(str);
        if (!bytes || bytes.length === 0) return;
        await download(VM_ADDR(), bytes);
        await saveSource(strBytes);
    };

    return (
        <HeaderComponent
            onToggleMenu={toggleMenu}
            menuVisible={menuVisible}
            onDownload={handleDownload}
        />
    );
}

export default Header;
